.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

.nav {
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__logo,
.nav__toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list {
    display: flex;
    column-gap: 2rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: 0.3s;
}

.nav__icon,
.nav__close,
.nav__toggle {
    display: none;
}

/* Active link */
.active-link,
.nav__link:hover {
    color: var(--title-color-dark)
}

/*===== BREAKPOINTS =====*/
@media screen and (max-width: 768px) {
    .header {
        top: initial;
        bottom: 0;
    }
    
    .nav {
        height: var(--header-height);
    }

    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 50%;
        width: 95%;
        background-color: var(--body-color);
        padding: 2rem 5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: 0.3s;
        transform: translate(-50%);
    }

    /* Show menu */
    .show-menu {
        bottom: 0;
    }

    .nav__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav__logo {
        font-size: 1.2rem;
    }

    .nav__icon {
        font-size: 1.2rem;
    }

    .nav__close {
        position: absolute;
        left: 50%;
        bottom: 0.5rem;
        font-size: 1.8rem;
        cursor: pointer;
        color: var(--title-color);
        transform: translate(-50%);
    }

    .nav__close:hover {
        color: var(--title-color-dark);
    }

    .nav__toggle {
        font-size: 1.5rem;
        cursor: pointer;
    }

    .nav__icon,
    .nav__close,
    .nav__toggle {
        display: block;
    }
}

@media screen and (max-width: 576px) {
    .nav__menu {
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 5rem;
        box-shadow: 0 -1px 7px rgba(0, 0, 0, 0.15);
        border-radius: 0rem 0rem 0 0;
    }

    .nav__list {
        column-gap: 1rem;
    }

    .nav__icon {
        font-size: 1.35rem;
    }

    .nav__close {
        font-size: 2rem;
        bottom: 0.8rem;
    }

    .nav__link {
        font-size: var(--normal-font-size);
    }
}

@media screen and (max-width: 350px) {
    .nav__menu {
        min-width: 280px;
    }

    .nav__list {
        column-gap: 0rem;
    }

}